<script setup>
import SafeTopContainer from "@/components/SafeTopContainer.vue"
import CustomTabbar from "@/components/CustomTabbar.vue"

const dataList = ref()

const options = ref()

onLoad(async (option) => {
  options.value = option

  if (!option.type || !option.method) {
    return
  }

  const r = await indexCate({
    method: option.method,
  })

  dataList.value = r.data
})

const handleClickCategory = (item) => {
  console.log(options.value)

  useNavigate("/pages/request/form", {
    type: options.value.type,
    method: options.value.method,
    category: item.id,
    categoryName: item.name,
  })
}

const handleClickBack = () => {
  uni.navigateBack()
}
</script>

<template>
  <view class="view-wrapper">
    <SafeTopContainer bgColor="#00569C">
      <view class="view-header flex items-center">
        <wd-icon
          name="thin-arrow-left"
          size="18px"
          @click="handleClickBack"
        ></wd-icon>
        <view style="margin-left: 32rpx"> Choose Category</view>
      </view>
    </SafeTopContainer>
    <view class="category-list-container">
      <view
        class="cate-item"
        v-for="(item, index) in dataList"
        :key="index"
        @click="handleClickCategory(item)"
      >
        <image :src="item.image" style="width: 160rpx; height: 160rpx" />
        <view
          class="cate-name"
          :style="{
            color: item.method == 3 || item.method == 4 ? '#069DB2' : '#D35C78',
          }"
        >
          {{ item.name }}
        </view>
      </view>
    </view>
  </view>
  <CustomTabbar />
</template>
